import React, { useState, useEffect } from 'react';
import { applicationsAPI } from '../services/api';
import { Application } from '../types';
import { useAuth } from '../context/AuthContext';
import toast from 'react-hot-toast';
import { usersAPI } from '../services/api';
import { User } from '../types';

const AdminDashboard: React.FC = () => {
  const [applications, setApplications] = useState<Application[]>([]);
  const [isLoading, setIsLoading] = useState(false);
  const [selectedAgent, setSelectedAgent] = useState<number | null>(null);
  const [agents, setAgents] = useState<User[]>([]);
  const { user, logout } = useAuth();

  useEffect(() => {
    loadApplications();
    loadAgents();
  }, []);

  const loadApplications = async () => {
    try {
      setIsLoading(true);
      const data = await applicationsAPI.getAll();
      setApplications(data);
    } catch (error: any) {
      toast.error('  ');
    } finally {
      setIsLoading(false);
    }
  };

  const loadAgents = async () => {
    try {
      const data = await usersAPI.getAllAgents();
      setAgents(data);
    } catch (error: any) {
      toast.error('  ');
    }
  };

  const handleStatusChange = async (applicationId: number, newStatus: string) => {
    try {
      await applicationsAPI.updateStatus(applicationId, newStatus);
      toast.success(' ');
      loadApplications();
    } catch (error: any) {
      toast.error('  ');
    }
  };

  const handleDelete = async (applicationId: number) => {
    if (!window.confirm(' ?')) return;
    try {
      await applicationsAPI.delete(applicationId);
      toast.success(' ');
      loadApplications();
    } catch (error: any) {
      toast.error('  ');
    }
  };

  const handleDeleteAgent = async (agentId: number) => {
    if (!window.confirm(' ?')) return;
    try {
      await usersAPI.delete(agentId);
      toast.success(' ');
      loadAgents();
    } catch (error: any) {
      toast.error('  ');
    }
  };

  const getStatusColor = (status: string) => {
    switch (status) {
      case 'new': return 'bg-gray-100 text-gray-800';
      case 'accepted': return 'bg-blue-100 text-blue-800';
      case 'success': return 'bg-green-100 text-green-800';
      case 'rejected': return 'bg-red-100 text-red-800';
      case 'paid': return 'bg-yellow-100 text-yellow-800';
      default: return 'bg-gray-100 text-gray-800';
    }
  };

  const getStatusText = (status: string) => {
    switch (status) {
      case 'new': return '';
      case 'accepted': return '';
      case 'success': return '';
      case 'rejected': return '';
      case 'paid': return '';
      default: return status;
    }
  };

  const filteredApplications = selectedAgent 
    ? applications.filter(app => app.agentId === selectedAgent)
    : applications;

  const uniqueAgents = Array.from(new Set(applications.map(app => app.agentId)))
    .map(agentId => applications.find(app => app.agentId === agentId)?.agent)
    .filter(Boolean);

  return (
    <div className="min-h-screen bg-gray-50">
      {/* Header */}
      <div className="bg-white shadow">
        <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8">
          <div className="flex justify-between items-center py-6">
            <div>
              <h1 className="text-2xl font-bold text-gray-900"> </h1>
              <p className="text-sm text-gray-600"> , {user?.fullName}</p>
            </div>
            <button
              onClick={logout}
              className="bg-red-600 text-white px-4 py-2 rounded-md text-sm font-medium hover:bg-red-700"
            >
              
            </button>
          </div>
        </div>
      </div>

      {/* Main Content */}
      <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8 py-8">
        {/*   */}
        <div className="bg-white shadow rounded-lg mb-8">
          <div className="px-4 py-5 sm:p-6">
            <h3 className="text-lg leading-6 font-medium text-gray-900 mb-4"></h3>
            {agents.length === 0 ? (
              <p className="text-gray-500"> </p>
            ) : (
              <ul className="divide-y divide-gray-200">
                {agents.map(agent => (
                  <li key={agent.id} className="flex justify-between items-center py-2">
                    <span>{agent.fullName} <span className="text-xs text-gray-400">({agent.username})</span></span>
                    <button
                      onClick={() => handleDeleteAgent(agent.id)}
                      className="bg-red-500 text-white px-3 py-1 rounded text-xs hover:bg-red-600"
                    >
                      
                    </button>
                  </li>
                ))}
              </ul>
            )}
          </div>
        </div>

        {/* Filters */}
        <div className="bg-white shadow rounded-lg mb-8">
          <div className="px-4 py-5 sm:p-6">
            <h3 className="text-lg leading-6 font-medium text-gray-900 mb-4">
              
            </h3>
            <div className="flex flex-wrap gap-4">
              <button
                onClick={() => setSelectedAgent(null)}
                className={`px-4 py-2 rounded-md text-sm font-medium ${
                  selectedAgent === null
                    ? 'bg-blue-600 text-white'
                    : 'bg-gray-200 text-gray-700 hover:bg-gray-300'
                }`}
              >
                 
              </button>
              {uniqueAgents.map((agent) => (
                <button
                  key={agent?.id}
                  onClick={() => setSelectedAgent(agent?.id || null)}
                  className={`px-4 py-2 rounded-md text-sm font-medium ${
                    selectedAgent === agent?.id
                      ? 'bg-blue-600 text-white'
                      : 'bg-gray-200 text-gray-700 hover:bg-gray-300'
                  }`}
                >
                  {agent?.fullName}
                </button>
              ))}
            </div>
          </div>
        </div>

        {/* Applications List */}
        <div className="bg-white shadow rounded-lg">
          <div className="px-4 py-5 sm:p-6">
            <div className="flex justify-between items-center mb-4">
              <h3 className="text-lg leading-6 font-medium text-gray-900">
                 {selectedAgent && ` ${uniqueAgents.find(a => a?.id === selectedAgent)?.fullName}`}
              </h3>
              <button
                onClick={loadApplications}
                className="bg-blue-600 text-white px-4 py-2 rounded-md text-sm font-medium hover:bg-blue-700"
              >
                
              </button>
            </div>
            
            {isLoading ? (
              <p className="text-gray-500 text-center py-8">...</p>
            ) : filteredApplications.length === 0 ? (
              <p className="text-gray-500 text-center py-8">  </p>
            ) : (
              <div className="space-y-4">
                {filteredApplications.map((app) => (
                  <div key={app.id} className="border rounded-lg p-4">
                    <div className="flex justify-between items-start">
                      <div className="flex-1">
                        <div className="flex items-center gap-2 mb-2">
                          <h4 className="font-medium text-gray-900">{app.clientFullName}</h4>
                          <span className="text-xs text-gray-500"> {app.agent?.fullName}</span>
                        </div>
                        <p className="text-sm text-gray-600">{app.clientAddress}</p>
                        <p className="text-sm text-gray-600">{app.clientPhone}</p>
                        {app.comment && (
                          <p className="text-sm text-gray-500 mt-2">{app.comment}</p>
                        )}
                        <p className="text-xs text-gray-400 mt-2">
                          : {new Date(app.createdAt).toLocaleDateString()}
                        </p>
                      </div>
                      <div className="flex flex-col items-end gap-2">
                        <span className={`px-2 py-1 text-xs font-medium rounded-full ${getStatusColor(app.status)}`}>
                          {getStatusText(app.status)}
                        </span>
                        <select
                          value={app.status}
                          onChange={(e) => handleStatusChange(app.id, e.target.value)}
                          className="text-xs border border-gray-300 rounded px-2 py-1 focus:outline-none focus:ring-1 focus:ring-blue-500"
                        >
                          <option value="new"></option>
                          <option value="accepted"></option>
                          <option value="success"></option>
                          <option value="rejected"></option>
                          <option value="paid"></option>
                        </select>
                        <button
                          onClick={() => handleDelete(app.id)}
                          className="mt-2 bg-red-500 text-white px-3 py-1 rounded text-xs hover:bg-red-600"
                        >
                          
                        </button>
                      </div>
                    </div>
                  </div>
                ))}
              </div>
            )}
          </div>
        </div>
      </div>
    </div>
  );
};

export default AdminDashboard; 