import React, { useState, useEffect } from 'react';
import { applicationsAPI } from '../services/api';
import { Application } from '../types';
import { useAuth } from '../context/AuthContext';
import toast from 'react-hot-toast';
import * as Dialog from '@radix-ui/react-dialog';
import { authAPI } from '../services/api';

const AgentDashboard: React.FC = () => {
  const [applications, setApplications] = useState<Application[]>([]);
  const [showSheet, setShowSheet] = useState(false);
  const [isLoading, setIsLoading] = useState(false);
  const [formData, setFormData] = useState({
    clientAddress: '',
    clientFullName: '',
    clientPhone: '',
    comment: '',
  });
  const [telegramChatId, setTelegramChatId] = useState('');
  const [isTelegramSaved, setIsTelegramSaved] = useState(false);
  const [showTelegramModal, setShowTelegramModal] = useState(false);
  const { user, logout, updateUser } = useAuth();

  // ,    Telegram
  useEffect(() => {
    if (user?.telegramChatId) {
      setIsTelegramSaved(true);
      setTelegramChatId(user.telegramChatId);
    }
  }, [user]);

  useEffect(() => {
    loadApplications();
    const interval = setInterval(loadApplications, 5000); //   5 
    return () => clearInterval(interval);
  }, []);

  const loadApplications = async () => {
    try {
      const data = await applicationsAPI.getMy();
      setApplications(data);
    } catch (error: any) {
      toast.error('  ');
    }
  };

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault();
    setIsLoading(true);

    try {
      await applicationsAPI.create(formData);
      toast.success('  !');
      setFormData({
        clientAddress: '',
        clientFullName: '',
        clientPhone: '',
        comment: '',
      });
      setShowSheet(false);
      loadApplications();
    } catch (error: any) {
      toast.error(error.response?.data?.message || '  ');
    } finally {
      setIsLoading(false);
    }
  };

  const handleChange = (e: React.ChangeEvent<HTMLInputElement | HTMLTextAreaElement>) => {
    setFormData({
      ...formData,
      [e.target.name]: e.target.value,
    });
  };

  const handleSaveTelegram = async (e: React.FormEvent) => {
    e.preventDefault();
    try {
      await authAPI.setTelegramChatId(telegramChatId);
      toast.success('Telegram chat_id  !');
      setIsTelegramSaved(true);
      setShowTelegramModal(false); //      
      
      //     
      if (user) {
        const updatedUser = { ...user, telegramChatId };
        updateUser(updatedUser);
      }
    } catch (error: any) {
      toast.error(error.response?.data?.message || '  Telegram');
    }
  };

  const getStatusColor = (status: string) => {
    switch (status) {
      case 'new': return 'bg-gray-100 text-gray-800';
      case 'accepted': return 'bg-blue-100 text-blue-800';
      case 'success': return 'bg-green-100 text-green-800';
      case 'rejected': return 'bg-red-100 text-red-800';
      case 'paid': return 'bg-yellow-100 text-yellow-800';
      default: return 'bg-gray-100 text-gray-800';
    }
  };

  const getStatusText = (status: string) => {
    switch (status) {
      case 'new': return '';
      case 'accepted': return '';
      case 'success': return '';
      case 'rejected': return '';
      case 'paid': return '';
      default: return status;
    }
  };

  return (
    <div className="min-h-screen bg-gray-50 relative">
      {/* Header */}
      <div className="bg-white shadow">
        <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8">
          <div className="flex justify-between items-center py-6">
            <div>
              <h1 className="text-2xl font-bold text-gray-900"> </h1>
              <p className="text-sm text-gray-600"> , {user?.fullName}</p>
            </div>
            <button
              onClick={logout}
              className="bg-red-600 text-white px-4 py-2 rounded-md text-sm font-medium hover:bg-red-700"
            >
              
            </button>
          </div>
        </div>
      </div>

      {/*  Telegram -   */}
      <div className="bg-white shadow rounded-lg mb-8 max-w-lg mx-auto">
        <div className="px-4 py-5 sm:p-6">
          <div className="flex justify-between items-center">
            <div>
              <h3 className="text-lg leading-6 font-medium text-gray-900">Telegram </h3>
              <p className="text-sm text-gray-600 mt-1">
                {isTelegramSaved ? ' ' : '    '}
              </p>
            </div>
            <button
              onClick={() => setShowTelegramModal(true)}
              className={`px-4 py-2 rounded-md text-sm font-medium ${
                isTelegramSaved
                  ? 'bg-green-100 text-green-800 hover:bg-green-200'
                  : 'bg-blue-600 text-white hover:bg-blue-700'
              }`}
            >
              {isTelegramSaved ? '' : ''}
            </button>
          </div>
        </div>
      </div>

      {/*     Telegram */}
      {showTelegramModal && (
        <div className="fixed inset-0 bg-black bg-opacity-50 flex items-center justify-center z-50 p-4">
          <div className="bg-white rounded-lg max-w-md w-full p-6">
            <h3 className="text-lg font-medium text-gray-900 mb-4"> Telegram</h3>

            <div className="space-y-4 mb-6">
              <div className="flex items-start space-x-3">
                <span className="bg-blue-100 text-blue-800 text-sm font-medium px-2 py-1 rounded-full">1</span>
                <div>
                  <p className="text-sm text-gray-700">      </p>
                  <a
                    href="https://t.me/agentprofit_bot"
                    target="_blank"
                    rel="noopener noreferrer"
                    className="text-blue-600 text-sm hover:underline"
                  >
                    @agentprofit_bot
                  </a>
                  <p className="text-xs text-gray-500 mt-1"> START</p>
                </div>
              </div>

              <div className="flex items-start space-x-3">
                <span className="bg-blue-100 text-blue-800 text-sm font-medium px-2 py-1 rounded-full">2</span>
                <div>
                  <p className="text-sm text-gray-700">  ID</p>
                  <a
                    href="https://t.me/getmyid_bot"
                    target="_blank"
                    rel="noopener noreferrer"
                    className="text-blue-600 text-sm hover:underline"
                  >
                    @getmyid_bot
                  </a>
                  <p className="text-xs text-gray-500 mt-1"> START,    ID</p>
                </div>
              </div>

              <div className="flex items-start space-x-3">
                <span className="bg-blue-100 text-blue-800 text-sm font-medium px-2 py-1 rounded-full">3</span>
                <div>
                  <p className="text-sm text-gray-700"> ID </p>
                </div>
              </div>
            </div>

            <form onSubmit={handleSaveTelegram} className="space-y-4">
              <input
                type="text"
                value={telegramChatId}
                onChange={e => setTelegramChatId(e.target.value)}
                placeholder=" Telegram ID"
                className="w-full border border-gray-300 rounded-md px-3 py-2 focus:outline-none focus:ring-2 focus:ring-blue-500"
                required
              />

              <div className="flex space-x-3">
                <button
                  type="submit"
                  className="flex-1 bg-blue-600 text-white py-2 px-4 rounded-md text-sm font-medium hover:bg-blue-700"
                >
                  
                </button>
                <button
                  type="button"
                  onClick={() => setShowTelegramModal(false)}
                  className="flex-1 bg-gray-300 text-gray-700 py-2 px-4 rounded-md text-sm font-medium hover:bg-gray-400"
                >
                  
                </button>
              </div>
            </form>
          </div>
        </div>
      )}

      {/* Applications List */}
      <div className="bg-white shadow rounded-lg">
        <div className="px-4 py-5 sm:p-6">
          <h3 className="text-lg leading-6 font-medium text-gray-900 mb-4">
             
          </h3>
          {applications.length === 0 ? (
            <p className="text-gray-500 text-center py-8">    </p>
          ) : (
            <div className="space-y-4">
              {applications.map((app) => (
                <div key={app.id} className="border rounded-lg p-4">
                  <div className="flex justify-between items-start">
                    <div className="flex-1">
                      <h4 className="font-medium text-gray-900">{app.clientFullName}</h4>
                      <p className="text-sm text-gray-600">{app.clientAddress}</p>
                      <p className="text-sm text-gray-600">{app.clientPhone}</p>
                      {app.comment && (
                        <p className="text-sm text-gray-500 mt-2">{app.comment}</p>
                      )}
                      <p className="text-xs text-gray-400 mt-2">
                        : {new Date(app.createdAt).toLocaleDateString()}
                      </p>
                    </div>
                    <span className={`px-2 py-1 text-xs font-medium rounded-full ${getStatusColor(app.status)}`}>
                      {getStatusText(app.status)}
                    </span>
                  </div>
                </div>
              ))}
            </div>
          )}
        </div>
      </div>

      {/* Floating Action Button + Dialog */}
      <Dialog.Root open={showSheet} onOpenChange={setShowSheet}>
        <Dialog.Trigger asChild>
          <button
            className="fixed bottom-6 right-6 z-40 bg-blue-600 hover:bg-blue-700 text-white rounded-full shadow-lg w-16 h-16 flex items-center justify-center text-3xl focus:outline-none focus:ring-2 focus:ring-blue-500"
            aria-label=" "
          >
            +
          </button>
        </Dialog.Trigger>
        <Dialog.Portal>
          <Dialog.Overlay className="fixed inset-0 bg-black/40 z-50 animate-fade-in" />
          <Dialog.Content
            className="fixed bottom-0 left-0 right-0 z-50 bg-white rounded-t-2xl shadow-lg max-w-lg mx-auto w-full animate-slide-up"
            style={{ maxHeight: '70vh', overflowY: 'auto' }}
          >
            <div className="flex flex-col items-center">
              <div className="w-12 h-1.5 bg-gray-300 rounded-full mb-2 mt-2" />
              <h3 className="text-lg font-medium text-gray-900"> </h3>
            </div>
            <form className="space-y-4 p-4" onSubmit={handleSubmit}>
              <div>
                <label className="block text-sm font-medium text-gray-700">
                    
                </label>
                <input
                  type="text"
                  name="clientAddress"
                  required
                  value={formData.clientAddress}
                  onChange={handleChange}
                  className="mt-1 block w-full border border-gray-300 rounded-md px-3 py-2 focus:outline-none focus:ring-blue-500 focus:border-blue-500"
                  placeholder="  "
                />
              </div>
              <div>
                <label className="block text-sm font-medium text-gray-700">
                   
                </label>
                <input
                  type="text"
                  name="clientFullName"
                  required
                  value={formData.clientFullName}
                  onChange={handleChange}
                  className="mt-1 block w-full border border-gray-300 rounded-md px-3 py-2 focus:outline-none focus:ring-blue-500 focus:border-blue-500"
                  placeholder=" "
                />
              </div>
              <div>
                <label className="block text-sm font-medium text-gray-700">
                   
                </label>
                <input
                  type="tel"
                  name="clientPhone"
                  required
                  value={formData.clientPhone}
                  onChange={handleChange}
                  className="mt-1 block w-full border border-gray-300 rounded-md px-3 py-2 focus:outline-none focus:ring-blue-500 focus:border-blue-500"
                  placeholder="+7 999 123-45-67"
                />
              </div>
              <div>
                <label className="block text-sm font-medium text-gray-700">
                  
                </label>
                <textarea
                  name="comment"
                  value={formData.comment}
                  onChange={handleChange}
                  rows={3}
                  className="mt-1 block w-full border border-gray-300 rounded-md px-3 py-2 focus:outline-none focus:ring-blue-500 focus:border-blue-500"
                  placeholder=" "
                />
              </div>
              <div className="flex gap-3">
                <button
                  type="submit"
                  disabled={isLoading}
                  className="flex-1 bg-blue-600 text-white py-2 px-4 rounded-md hover:bg-blue-700 disabled:opacity-50"
                >
                  {isLoading ? '...' : ''}
                </button>
                <Dialog.Close asChild>
                  <button
                    type="button"
                    className="flex-1 bg-gray-300 text-gray-700 py-2 px-4 rounded-md hover:bg-gray-400"
                  >
                    
                  </button>
                </Dialog.Close>
              </div>
            </form>
          </Dialog.Content>
        </Dialog.Portal>
      </Dialog.Root>
    </div>
  );
};

export default AgentDashboard; 