import axios from 'axios';
import { LoginData, RegisterData, CreateApplicationData, AuthResponse, Application } from '../types';

const API_URL = process.env.REACT_APP_API_URL || 'http://localhost:4000/api';

const api = axios.create({
  baseURL: API_URL,
  headers: {
    'Content-Type': 'application/json',
  },
});

//    
api.interceptors.request.use((config) => {
  const token = localStorage.getItem('token');
  if (token) {
    config.headers.Authorization = `Bearer ${token}`;
  }
  return config;
});

export const authAPI = {
  login: async (data: LoginData): Promise<AuthResponse> => {
    const response = await api.post('/auth/login', data);
    return response.data;
  },

  register: async (data: RegisterData): Promise<AuthResponse> => {
    const response = await api.post('/auth/register', data);
    return response.data;
  },

  getMe: async () => {
    const response = await api.get('/auth/me');
    return response.data;
  },

  setTelegramChatId: async (chatId: string) => {
    const response = await api.post('/auth/set-telegram', { chatId });
    return response.data;
  },
};

export const applicationsAPI = {
  create: async (data: CreateApplicationData): Promise<{ message: string; application: Application }> => {
    const response = await api.post('/applications', data);
    return response.data;
  },

  getMy: async (): Promise<Application[]> => {
    const response = await api.get('/applications/my');
    return response.data;
  },

  getAll: async (): Promise<Application[]> => {
    const response = await api.get('/applications/all');
    return response.data;
  },

  getByAgent: async (agentId: number): Promise<Application[]> => {
    const response = await api.get(`/applications/agent/${agentId}`);
    return response.data;
  },

  updateStatus: async (id: number, status: string): Promise<{ message: string; application: Application }> => {
    const response = await api.patch(`/applications/${id}/status`, { status });
    return response.data;
  },

  delete: async (id: number): Promise<{ message: string }> => {
    const response = await api.delete(`/applications/${id}`);
    return response.data;
  },
};

export const usersAPI = {
  getAllAgents: async () => {
    const response = await api.get('/auth/agents');
    return response.data;
  },
  delete: async (id: number) => {
    const response = await api.delete(`/auth/${id}`);
    return response.data;
  },
};

export default api; 